* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background: #23252C;
}

.app-wrapper {
    font-family: Inter, sans-serif;
    margin: 20px auto;
    width: 320px;
    background: #1A1B21;
    border-radius: 10px;
}

.info--wrapper {
    text-align: center;
}

.info--portrait {
    width: 100%;
    height: 320px;
    object-fit: cover;
    object-position: bottom;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.info--name, .info--title, .info--website {
    margin: 0;
}

.info--name {
    font-size: 25px;
    color: #FFFFFF;
    margin-top: 20px;
}

.info--title {
    font-size: 13px;
    color: #F3BF99;
    margin-top: 5px;
}

.info--website {
    font-size: 10px;
    color: #F5F5F5;
    text-decoration: none;
}

.info--btn-wrapper button {
    margin: 15px 8px 0 8px;
    width: 115px;
    padding-top: 8px;
    padding-bottom: 8px;
    border-radius: 6px;
    border: none;
    font-size: 14px;
    font-family: Inter, sans-serif
}

.info--email-btn {
    background: #FFFFFF;
    color: #374151;
}

.info--linkedin-btn {
    background: #5093E2;
    color: #FFFFFF;
}

.about--wrapper {
    color: #F5F5F5;
    padding: 35px 35px 24px 35px;
}

.about--header {
    font-size: 16px;
}

.about--p {
    font-size: 10px;
}

.footer--wrapper {
    height: 64px;
    background: #161619;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding: 0 72px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.footer--wrapper > a > img {
    height: 25px;
}